<template>
  <div class="dtgbtp">
     <div class="change">
      <div class="title">对比度选择</div>
       <input type="range" name="huerotate" min='0' max="359" value="0" data-sizing='deg'>
       <div class="title">颜色选择</div>
       <input type="color" name="base" value="#00FF00">
     </div>
      <img src="../../assets/images/0.jpg">
  </div>
</template>

<script>
  export default{
    mounted(){
      const inputs = document.querySelectorAll('.change input');

      function handleUpdate() {
        const suffix = this.dataset.sizing || '';
        document.documentElement.style.setProperty(`--${this.name}`,this.value + suffix);
      }

      inputs.forEach((input) =>{
        input.addEventListener('input', handleUpdate)
      });
      /*inputs.forEach((input) =>{
        input.addEventListener('mousemove', handleUpdate)
    });*/
    }
  }
</script>

<style lang="scss" scoped>
:root{
  --base:#ff0000;
  --huerotate:0;
}

  img{
    padding: 10px;
    background: var(--base);
    box-sizing:border-box;
    width: 100%;
    filter: hue-rotate(var(--huerotate));
    box-shadow:0 3px 2px 1px rgba(0,0,0,.1);
  }

  
  input[type=range]{ 
    outline: none; 
    -webkit-appearance: none;/*清除系统默认样式*/  
    width:100% !important;  
    background:#ddd; 
    height:.06rem; 
  } 

  input[type=range]::-webkit-slider-thumb {  
   -webkit-appearance: none; 
   height:16px;/*拖动块高度*/  
   width: 16px;/*拖动块宽度*/  
   background:  white;/*拖动块背景*/  
   border-radius: 50%; /*外观设置为圆形*/  
   border: solid 0.04rem green; /*设置边框*/  
}
   input[type="color"]{
    -webkit-appearance: none; 
    background:none;
    width: .8rem;
    height: .8rem;
   }

.change{
  input{
    margin: .4rem 0;
  }
  .title{
    font-size: .26rem;
    color:var(--base);
  }
}
</style>

